import React from 'react'
import TransportWebHID from "@ledgerhq/hw-transport-webhid";
import Iota from 'hw-app-iota'

class LedgerTrampoline extends React.Component {

  constructor() {
    super()
    this.registerEventListener = this.registerEventListener.bind(this)
    this.initLedger = this.initLedger.bind(this)
    this.getNewAddress = this.getNewAddress.bind(this)
    this.sendMessageToExtension = this.sendMessageToExtension.bind(this)
    this.registerEventListener()

    this.path = "44'/4218'/0'/0'"
  }

  registerEventListener() {
    window.addEventListener('message', e => {
      console.log(e)
      if (e && e.data && e.data.target === 'ledger-iframe') {
        console.log(e)
        const action = e.data.action
        const params = e.data.params

        const replyAction = action + '-reply'
        switch (action) {
          case 'ledger-init': 
            this.initLedger(replyAction)
            break
          case 'ledger-new-address': 
            this.getNewAddress(params.index, replyAction)
            break
          default:
            this.sendMessageToExtension({
              action: replyAction,
              success: false,
              payload: null
            })
        }
      }
    }, false)
  }

  async initLedger(replyAction){
    try {
      const transport = await TransportWebHID.create()
      this.iota = new Iota(transport)
      this.sendMessageToExtension({
        action: replyAction,
        success: true,
        payload: null
      })
    } catch (e) {
      this.sendMessageToExtension({
        action: replyAction,
        success: false,
        payload: e.message
      })
    }
  }

  async getNewAddress(index, replyAction) {
    try {
      const transport = await TransportWebHID.create()
      this.iota = new Iota(transport)
      await this.iota.setActiveSeed(this.path)
      const address = this.iota.getAddress(index, {checksum: true})
      console.log(address)
      this.sendMessageToExtension({
        action: replyAction,
        success: true,
        payload: address
      })
    } catch(e) {
      this.sendMessageToExtension({
        action: replyAction,
        success: false,
        payload: e
      })
    }
  }

  sendMessageToExtension(msg) {
    window.parent.postMessage(msg, '*')
  }

  render() {
    return null
  }
}

export default LedgerTrampoline